.header-sub-page {
	position: relative;
	display: flex;
	padding-top: 150px;
	height: auto;
}
.header-sub-page-text {
	position: relative;
	margin: 0 45px;
	width: 100%;
	z-index: 3;
	p {
		padding-top: 1em;
		margin-bottom: 20px;
		max-width: 600px;
	}
	h1 {
		font-size: 10vw;
	}
}
.default-image {
	position: absolute;
	width: 130%;
	height: 85%;
	right: -140%;
	border: 1.3em #ffffff solid;
	box-shadow: 0.5em 0.2em 1.3em #000000;
	z-index: 4;
	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}
.extra-content {
	li {
		font-family: bebas-neue-v14-deprecated, sans-serif;
		font-size: 10vw;
		line-height: 10vw;
		transition: all 0.5s ease-in-out;
	}
	a {
		position: relative;
		overflow: hidden;
		display: block;
		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: -100%;
			width: 20px;
			height: 100%;
			background-color: #C1272D;
			transition: all 0.5s ease-in-out;
		}
		&:hover {
			li {
				margin-left: 40px;
				transition: all 0.5s ease-in-out;
			}
			&::before {
				left: 0;
				transition: all 0.5s ease-in-out;
			}
		}
	}
}
.header-sub-page-img {
	position: sticky;
	display: flex;
	object-fit: cover;
	overflow: hidden;
	right: 0;
	top: 15%;
	height: 65vh;
	width: 100%;
	padding: 25px;
	z-index: 3;
	> div {
		position: absolute;
		width: 130%;
		height: 79%;
		right: -140%;
		border: 1.3em #ffffff solid;
		box-shadow: 0.5em 0.2em 1.3em #000000;
		z-index: 4;
		transition: all 1s ease-in-out;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.proj-caption {
		display: flex;
		flex-direction: column;
		position: absolute;
		bottom: 11%;
		left: 4em;
		z-index: 4;
		color: #ffffff;
	}
	.show-img {
		right: -40%;
		transition: all 1s ease-in-out .8s;
	}
	.hide-img {
		right: -140%;
		transition: all 1s ease-in-out;
	}
}
@keyframes slide-hover {
		0% {
      left: -100%;
	}
    100% {
        left: 0;
    }
}
